import { ChangeDetectionStrategy, ChangeDetectorRef, Component, ElementRef, NgZone, ViewChild } from "@angular/core";

import { App, NavController, Platform } from "ionic-angular";

import { AppConstants } from "@app/app.constants";
import { AppGlobals } from "@app/app.globals";
import { AppLocales } from "@app/app.locales";

import { MarsAuthService } from "@services/auth.service";
import { MarsInteractionService } from "@services/interaction.service";
import { MarsNavigationService } from "@services/navigation.service";

import { MarsAddressAutocompleteDirective } from "@directives/mars-address-autocomplete";

import { Backend } from "@backend/index";
import { AppUserPages } from "@pages/user-pages";


@Component({
    selector: "page-user-type-information",
    templateUrl: "user-type-information.html",
    changeDetection: ChangeDetectionStrategy.OnPush
})

export class UserTypeInformationPage {

    navigationService: MarsNavigationService;
    translations: AppTranslations;
    subpackage: string = AppConstants.SUBPACKAGE;
    previousStep: string;
    nextStep: string;
    spinner: any;
    showAddressSpinner = false;
    windowIsDesktopSized = window.innerWidth > 768;
    user: any = {
        role: AppConstants.MERCHANT_ROLE,
        merchantType: "MERCHANT",
        kind: {},
        paymentData: {},
        takeThePetAtHome: false,
        documents: [{}],
        services: []
    };

    constructor(private platform: Platform,
        private app: App,
        private zone: NgZone,
        private navCtrl: NavController,
        private changeDetector: ChangeDetectorRef,
        private locales: AppLocales,
        private authService: MarsAuthService,
        private globals: AppGlobals,
        private userInformationPages: AppUserPages,
        private interactionService: MarsInteractionService) {
        this.navigationService = new MarsNavigationService(this.app);
        this.navigationService.setNavCtrl(this.navCtrl);
        this.translations = this.locales.load();
    }

    ionViewWillEnter() {
        this.initialize();
    }

    ionViewDidEnter() {
        setTimeout(() => { this.changeDetector.detectChanges(); }, 200);
    }

    initialize() { }

    onKindSelected(kind: string) {
        switch (kind) {
            case ("CUSTOMER"):
                this.navigationService.goTo("CustomerBasicInformationPage");
                break;
            case ("ONG"):
                this.navigationService.goTo("OngBasicInformationPage");
                break;
            case ("MERCHANT"):
                this.navigationService.goTo("MerchantBasicInformationPage", { roles: `["MERCHANT"]` });
                break;
            case ("SERVICE_ESTABLISHMENT"):
                this.navigationService.goTo("MerchantBasicInformationPage", { roles: `["SERVICE_ESTABLISHMENT"]` });
                break;
            case ("MERCHANT_AND_SERVICE_ESTABLISHMENT"):
                this.navigationService.goTo("MerchantBasicInformationPage", { roles: `["MERCHANT","SERVICE_ESTABLISHMENT"]` });
                break;
            case ("SERVICE_PROVIDER"):
                this.navigationService.goTo("MerchantBasicInformationPage", { roles: `["SERVICE_PROVIDER"]` });
                break;
        }
    }

    async save() {
        try {
            this.spinner = this.interactionService.spinner({ content: `${this.translations.loading}...` });
            let isLoggedIn = this.authService.isLoggedIn();
            let user = isLoggedIn ? (await Backend.updateUser({ user: this.user, xAccessToken: MarsAuthService.getMarsToken() })).data : (await Backend.createUser({ user: this.user })).data;
            this.storeDataFor(user);
            return MarsAuthService.finishedSignup() ? this.navigationService.goBack() : this.navigationService.goTo(this.nextStep);
        } catch (e) {
            this.interactionService.alert(this.translations.server_failure);
        } finally {
            this.spinner.dismiss();
            this.changeDetector.detectChanges();
        }
    }

    storeDataFor(user) {
        MarsAuthService.setLoggedInUser(user);
        MarsAuthService.setMarsToken(user.token);
    }

    goBack() {
        this.navigationService.setRoot(this.previousStep);
    }
}



